/*==================================================================================
  SITE STYLES
==================================================================================*/
/**
 * General CSS File
 * Contains defaults and presets
 *
 * The Idea is to keep editing in this file to a
 * minimum while using vars.scss for very basic styling.
 *
 */
/*==================================================================================
  General
==================================================================================*/
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain;
  -webkit-tap-highlight-color: transparent;
}

html {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: -webkit-fill-available;
  font-family: "Gilroy";
  font-size: 100%;
}

body {
  display: block;
  background: var(--c-background);
  color: var(--c-text);
  cursor: default;
  min-height: 100vh;
  min-height: -webkit-fill-available;
  -webkit-overflow-scrolling: auto;
}

html,
body {
  height: 100%;
}
@media (max-width: 991.98px) {
  html,
body {
    height: auto !important;
  }
}

.site {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
}

.content {
  padding: 4rem 0;
}
@media (max-width: 575.98px) {
  .content {
    padding: 1rem 0;
  }
}

.content-top {
  padding: 4rem 0 0;
}
@media (max-width: 575.98px) {
  .content-top {
    padding: 1rem 0 0 0;
  }
}

.content-bottom {
  padding: 0 0 4rem;
}
@media (max-width: 575.98px) {
  .content-bottom {
    padding: 0 0 1rem;
  }
}

.no-padding {
  padding: 0;
}

.entry-content {
  font-size: 1rem;
}
.entry-content table {
  width: 100%;
}
.entry-content table td {
  border-color: #7c7a7a;
}
.entry-content p {
  margin: 1rem 0;
  line-height: 160%;
}
.entry-content a {
  color: var(--c-accent);
}
.entry-content a:hover {
  text-decoration: none;
}
.entry-content ul {
  position: relative;
  margin: 1.5rem;
  list-style: none;
}
.entry-content ul li {
  margin: 0.5rem 0;
}
.entry-content ul li::before {
  content: "•";
  /* Add content: \2022 is the CSS Code/unicode for a bullet */
  color: var(--c-accent);
  /* Change the color */
  font-weight: bold;
  /* If you want it to be bold */
  display: inline-block;
  /* Needed to add space between the bullet and the text */
  width: 1em;
  /* Also needed for space (tweak if needed) */
  margin-left: -1em;
  /* Also needed for space (tweak if needed) */
}

button:focus {
  outline: 0;
}

select {
  cursor: pointer;
  width: 100%;
}

select,
input,
textarea {
  display: flex;
  align-items: center;
  background: var(--c-second);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
  color: var(--c-dark);
  border: none;
  padding: 5px;
  margin: 0;
  outline: none;
}

img[loading=lazy] {
  animation: show 1s;
}

@keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  pointer-events: none;
}
.cover.dark::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  z-index: 2;
}
.cover img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.notfound {
  text-align: center;
}

@media (max-width: 991.98px) {
  .container {
    max-width: none;
  }

  .row > * {
    padding-right: var(--bs-gutter-x);
    padding-left: var(--bs-gutter-x);
  }
}
.notfound {
  padding: 2rem;
}

/*==================================================================================
  Preloader
==================================================================================*/
.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 999;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background: linear-gradient(219.63deg, #643b55 -3.55%, #182336 32.72%, #05080d 72.22%, #000000 86.9%), #040609;
  padding: 4vw;
  will-change: transform, opacity;
}
.preloader h6 {
  font-size: 1.125rem;
  font-style: italic;
  font-weight: 500;
  text-align: center;
  margin: 1.5rem 0 0;
}
.preloader.hidden {
  pointer-events: none;
}

.preloader__logo {
  margin: 4rem 0 0;
}

.preloader__percent {
  font-size: 1.125rem;
}

/*==================================================================================
  Header
==================================================================================*/
.site__header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 2vw 3.5vw;
  z-index: 99;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 991.98px) {
  .site__header {
    padding: 1rem 1.5rem;
  }
}

.site__header-branding {
  transition: all ease-out 0.3s;
}
.site__header-branding img {
  display: block;
}
@media (min-width: 992px) {
  .site__header-branding:hover {
    transform: scale(1.01);
  }
}

.site__header-menu-button {
  display: none;
}
@media (max-width: 991.98px) {
  .site__header-menu-button {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: none;
    border: none;
    cursor: pointer;
    margin: 0 0 0 auto;
  }
  .site__header-menu-button .line {
    width: 32px;
    height: 3px;
    background: var(--c-light);
    border-radius: 10px;
    margin: 5px 0;
    transition: all ease-out 0.3s;
    will-change: transform;
  }
  .site__header-menu-button[aria-expanded=true] .line:nth-child(1) {
    transform: rotate(45deg) translate(5px, 4px);
  }
  .site__header-menu-button[aria-expanded=true] .line:nth-child(2) {
    transform: rotate(-45deg) translate(5px, -4px);
  }
}

.site__header-navigation {
  flex: 1;
}

.site__header-nav-box {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  margin: 0 0 0 2rem;
}
@media (max-width: 991.98px) {
  .site__header-nav-box {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    margin: 0;
    padding: 3.5rem 1rem 1rem;
    z-index: -1;
    visibility: hidden;
    opacity: 0;
    will-change: opacity;
    flex-direction: column;
    transition: all ease-out 0.3s;
  }
  .site__header-nav-box.active {
    width: 100%;
    opacity: 1;
    visibility: visible;
    background: rgba(20, 30, 46, 0.98);
  }
}

@media (max-width: 991.98px) {
  .site__header-menu {
    width: 100%;
  }
}
.site__header-menu > ul {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  list-style: none;
}
@media (max-width: 991.98px) {
  .site__header-menu > ul {
    flex-direction: column;
    align-items: stretch;
    justify-content: space-between;
    width: 100%;
  }
}
.site__header-menu > ul > li {
  position: relative;
  padding: 1rem;
}
@media (max-width: 991.98px) {
  .site__header-menu > ul > li {
    border-top: 1px solid #3b3064;
  }
}
.site__header-menu > ul > li > a {
  color: var(--c-light);
  text-decoration: none;
  font-size: 1rem;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 0.04em;
  transition: all ease-out 0.3s;
}
.site__header-menu > ul > li > a:hover {
  color: var(--c-accent);
}
@media (min-width: 992px) {
  .site__header-menu > ul > li > ul {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    bottom: 0;
    left: 0;
    width: max-content;
    list-style: none;
    background: rgba(20, 30, 46, 0.92);
    border-radius: 10px;
    padding: 0.4rem 0;
    z-index: 9;
    transform: translate(0, 95%);
    will-change: transform, opacity;
    transition: all ease-out 0.3s;
    opacity: 0;
    visibility: hidden;
  }
  .site__header-menu > ul > li > ul > li > a {
    display: block;
    color: #8699bb;
    text-decoration: none;
    font-size: 1rem;
    font-weight: 600;
    letter-spacing: 0;
    transition: all ease-out 0.3s;
    padding: 0.4rem 1rem;
  }
  .site__header-menu > ul > li > ul > li > a:hover {
    color: var(--c-accent);
  }
}
@media (max-width: 991.98px) {
  .site__header-menu > ul > li > ul {
    list-style: none;
    padding: 0.4rem 0 0;
    display: none;
  }
  .site__header-menu > ul > li > ul.active {
    display: block;
  }
  .site__header-menu > ul > li > ul > li > a {
    display: block;
    color: var(--c-light);
    text-decoration: none;
    padding: 0.4rem;
    font-weight: 500;
    transition: all ease-out 0.3s;
  }
  .site__header-menu > ul > li > ul > li > a:hover {
    color: var(--c-accent);
  }
}
.site__header-menu > ul > li.menu-item-has-children {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
@media (max-width: 991.98px) {
  .site__header-menu > ul > li.menu-item-has-children {
    flex-direction: column;
    align-items: flex-start;
    text-align: left;
  }
  .site__header-menu > ul > li.menu-item-has-children > span {
    position: absolute;
    top: 10px;
    right: 0;
    width: 40px;
    height: 35px;
    display: block;
    cursor: pointer;
    background: url(assets/dist/svg/dropdown.svg) no-repeat center;
    background-size: 20px;
    z-index: 1;
    transition: all ease-out 0.3s;
  }
  .site__header-menu > ul > li.menu-item-has-children > span.active {
    transform: scaleY(-1);
  }
}
@media (min-width: 992px) {
  .site__header-menu > ul > li.menu-item-has-children:hover > a::after {
    transform: scaleY(-1);
  }
  .site__header-menu > ul > li.menu-item-has-children:hover > a {
    color: var(--c-accent);
  }
  .site__header-menu > ul > li.menu-item-has-children:hover ul {
    visibility: visible;
    transform: translate(0, 100%);
    opacity: 1;
  }
}
.site__header-menu > ul > li.menu-item-has-children > a {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
@media (min-width: 992px) {
  .site__header-menu > ul > li.menu-item-has-children > a {
    justify-content: space-between;
    text-align: left;
  }
  .site__header-menu > ul > li.menu-item-has-children > a::after {
    content: "";
    margin: 0 0 0 7px;
    width: 7px;
    height: 5px;
    background: url(assets/dist/svg/dropdown.svg) no-repeat center;
    transition: all ease-out 0.3s;
  }
}
@media (max-width: 991.98px) {
  .site__header-menu.menu-left ul li:first-child {
    border: none;
  }
}

/*==================================================================================
  Home slides
==================================================================================*/
.panel {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 6vw 3.5vw;
  overflow-y: auto;
  overflow-x: hidden;
  will-change: transform, opacity;
}
@media (max-width: 991.98px) {
  .panel {
    position: relative;
    min-height: 100vh;
    opacity: 1 !important;
    transform: none !important;
    visibility: visible !important;
    overflow: hidden;
    padding: 3rem 0;
    min-height: auto;
  }
}

.panel:not(.first) {
  opacity: 0;
  visibility: hidden;
  transform: scale(0.8);
}

/* Molecule */
.molecule {
  position: relative;
  width: 31.944vw;
  height: 38.889vw;
  pointer-events: none;
  margin: 0 0 0 10vw;
  transform: scale(0.8);
  transition: all ease-out 0.7s;
}
.molecule.molecule-2 .atom-3::after {
  background: #8757ac;
}
.molecule.molecule-2 .atom-1::before {
  width: 23vw;
}
.molecule.molecule-2 .atom-2::before {
  width: 23vw;
}
.molecule.molecule-2 .atom-3::before {
  width: 15vw;
}
.molecule.molecule-2 .atom-5::before {
  width: 15vw;
}
.molecule.molecule-3 .atom::before {
  background: #b67de4;
}
.molecule.molecule-3 .atom::after {
  background: #8757ac;
}
.molecule.molecule-3 .atom-1::before {
  width: 23vw;
}
.molecule.molecule-3 .atom-2::before {
  width: 23vw;
}
.molecule.molecule-3 .atom-3::before {
  width: 15vw;
}
.molecule.molecule-3 .atom-5::before {
  width: 15vw;
}
@media (max-width: 991.98px) {
  .molecule {
    margin: 5rem auto;
    transform: scale(1.2) !important;
  }
}
@media (max-width: 575.98px) {
  .molecule {
    transform: scale(1.8) !important;
  }
}

@keyframes impulse {
  0% {
    filter: blur(40px);
  }
  100% {
    filter: blur(0px);
  }
}
.atom {
  position: absolute;
  width: 4.722vw;
  height: 4.722vw;
  transition: all linear 1s;
  z-index: 1;
}
.atom::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 23vw;
  height: 0.208vw;
  background: #fff;
  transform-origin: top left;
  opacity: 0.8;
  transition: all ease-out 1s 0.3s;
}
.atom::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #94afd7;
  box-shadow: inset 0 0 1vw 5px white;
  border-radius: 50%;
  z-index: 2;
  transition: all linear 0.3s;
}
.atom.atom-1 {
  top: 0;
  left: 10%;
  z-index: 2;
}
.atom.atom-1::before {
  width: 23vw;
  height: 0.208vw;
  transform: rotate(58.7deg) translate(10%, -0.8vw);
}
.atom.atom-2 {
  top: 60%;
  left: 0%;
}
.atom.atom-2::before {
  width: 23vw;
  height: 0.208vw;
  transform: rotate(-82deg) translate(0, 2.5vw);
}
.atom.atom-3 {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
}
.atom.atom-3::before {
  top: 0.2vw;
  left: 1vw;
  width: 15vw;
  height: 0.208vw;
  transform: rotate(22deg) translate(10%, 1.3vw);
}
.atom.atom-4 {
  top: 58%;
  right: 0;
  z-index: 3;
}
.atom.atom-4::before {
  display: none;
}
.atom.atom-5 {
  bottom: 0;
  left: 50%;
  z-index: -1;
}
.atom.atom-5::before {
  width: 15vw;
  height: 0.208vw;
  transform: rotate(-97.5deg) translate(-5%, 1.8vw);
}

@keyframes dash {
  from {
    stroke-dashoffset: 2000;
  }
  to {
    stroke-dashoffset: 0;
  }
}
.molecule-overflow {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0 11vw 0 0;
  opacity: 0;
  pointer-events: none;
  will-change: transform, opacity;
  transform: rotate(5deg) scale(0.9);
  transition: all ease-out 0.7s;
}
.molecule-overflow svg {
  position: absolute;
  top: -40%;
  right: 5vw;
  opacity: 0.1;
}
.molecule-overflow .atom-1::before,
.molecule-overflow .atom-2::before,
.molecule-overflow .atom-3::before,
.molecule-overflow .atom-5::before {
  width: 1px;
}
.molecule-overflow.active {
  opacity: 1;
  transform: rotate(0deg) scale(1);
}
.molecule-overflow.active svg path {
  stroke-dasharray: 2000;
  animation: dash 5s linear;
}
@media (max-width: 991.98px) {
  .molecule-overflow {
    display: none;
  }
}

/* Backgrounds */
.fix-bg,
.stars-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  pointer-events: none;
}
.fix-bg img,
.stars-bg img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.stars-bg {
  transition: all ease-out 1s;
  opacity: 0;
  transform: scale(1.3);
  will-change: transform, opacity;
}
.stars-bg.active {
  opacity: 1;
  transform: scale(1);
}
.stars-bg.star-2 {
  transform: scale(1.1);
}

/* Slides */
.panel-0.active .home__zero-image img {
