  font-weight: 500;
  text-align: center;
  max-width: 890px;
  margin: -10vw auto 0;
  line-height: 1.3;
}
@media (max-width: 991.98px) {
  .home__zero-title {
    margin: 6rem auto 0;
    font-size: 1.625rem;
  }
}

.home__zero-image {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: auto;
  max-height: 35vh;
  overflow: hidden;
  z-index: -1;
}
.home__zero-image img {
  width: 100%;
  height: 100%;
