  height: 100%;
  object-fit: contain;
}
@media (max-width: 991.98px) {
  .home__head-image {
    position: relative;
    transform: scale(0.8) translate(0, 0);
  }
}

.home__slide-one {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-between;
}
@media (max-width: 991.98px) {
  .home__slide-one {
    align-items: flex-start;
    gap: 1rem;
  }
}
@media (max-width: 575.98px) {
  .home__slide-one {
    flex-direction: column;
    align-items: flex-start;
    gap: 0;
    padding: 0 1.5rem;
  }
}
.home__slide-one h2 {
  position: relative;
  z-index: 1;
  max-width: 480px;
  font-size: 2.75rem;
  font-weight: 500;
  pointer-events: none;
}
@media (max-width: 991.98px) {
  .home__slide-one h2 {
    font-size: 1.625rem;
  }
}
.home__slide-one h3 {
  position: relative;
  z-index: 1;
  max-width: 260px;
  font-size: 1.125rem;
  font-weight: 600;
  line-height: 1.4;
  pointer-events: none;
}
@media (max-width: 991.98px) {
  .home__slide-one h3 {
    font-size: 1rem;
    color: #8699bb;
