.button__scrolldown {
  position: fixed;
  left: 50%;
  bottom: 3vw;
  background: rgba(19, 28, 44, 0.47);
  border: 3px solid #192131;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all ease-out 0.7s;
  transform: translate(-50%, -50%);
